<template id="main-template"> 
  <div class="toast" v-show="show">{{txt}}</div>
</template>
<script>
var Vue = require('vue');
export default {
  template: '#main-template',
  props: {
      showToast: {
        type: Boolean,
        required: true,
        twoWay: true    
      },
      txt: {
        type: String,
        required: true,
        twoWay: true    
      },
  },
  data: function () {
    // return { 
    //   txt: this.show
    // }
  },
  computed:{
    show:function(){
      return this.showToast;
    }
  },
  updated:function(){
    if(this.show){
      var _this=this;
      setTimeout(function(){
          _this.closeToast();
      },2000)
    }
  },
  methods:{
    closeToast:function(){
      this.show=false;
      this.txt='';
      this.$emit( 'closeToast',false);
    }
  }
}
</script>

<style >
.toast {position:fixed;top: 6.2rem;width:7.5rem;left:1.25rem;z-index:99;height:.8rem/* 30px */;line-height:0.8rem;border-radius: .16rem/* 6px */;background:rgba(0,0,0,.45);font-size:.34666667rem;color: #fff;text-align: center;
  animation:ani_fadeout 2s both;
  -webkit-animation:ani_fadeout 2s both;
}
/*animation*/
@keyframes ani_fadeout
{
0% {opacity:0}
50% {opacity: 1}
100% {opacity:0}
}
@weikit-keyframes ani_fadeout
{
0% {opacity:0}
50% {opacity: 1}
100% {opacity:0}
}

</style>



