import Vue from 'vue'
import App from './App'

/* eslint-disable no-new */
import VueRouter from "vue-router";
import VueResource from 'vue-resource'

//开启debug模式
//Vue.config.debug = true;

Vue.use(VueRouter);
Vue.use(VueResource);

// 定义组件, 也可以像教程之前教的方法从别的文件引入
import pagePay from './pages/pay/app.vue'
import resultPage from './pages/result/app.vue'

// const routes = [
//   { path: '/first', component: First },
//   { path: '/second', component: secondcomponent }
// ]
// const router = new VueRouter({
//   routes // （缩写）相当于 routes: routes
// })
// 创建一个路由器实例
// 并且配置路由规则
var path=window.path='/dist';
const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: path+'/result',
      name: 'result',
      component: resultPage
    },
    {
      path: path+'/pay',
      component: pagePay
    },
    
    
  ]
})
// router.propTypes = {
//     history: PropTypes.any,
// };
// const app = new Vue({
//   router
// }).$mount('#app')

// 现在我们可以启动应用了！
// 路由器会创建一个 App 实例，并且挂载到选择符 #app 匹配的元素上。
const app = new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')
// new Vue({
//   el: '#app',
//   template: '<App/>',
//   components: { App }
// })



