<template id="main-template"> 
  <div class="loading_wrapper" v-show="show" transition="loading">
    <!-- <div class="loading"> -->
      <img src="../assets/loading.gif">
    <!-- </div> -->
  </div>
</template>
<script>
var Vue = require('vue');
export default {
  template: '#main-template',
  props: {
      show: {
        type: Boolean,
        required: true,
        twoWay: true    
      },
  },
}
</script>

<style lang="less">
.loading_wrapper {
  position:fixed;
  //display:table;
  top:0;
  width:100%;
  left:0;
  z-index:99;
  height:100%;
  background:rgba(0,0,0,.45);
  text-align: center;
  img {
    position:absolute;
    top:5rem;
    width:1rem;
    left: 4.5rem;
  }
}

.loading-enter, .loading-leave {
  opacity: 0;
}

.loading-enter .loading-container,
.loading-leave .loading-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>



