<template>
    <div id="app" v-cloak class="page-index result-page">
        <div class="header"> 支付详情<span class="finish-icon"></span></div>
        <div class="page-result" v-if="orderInfo.isSuccess==1">
            <div class="result-info">提示：请将该页出示服务人员确认支付结果！</div>
            <img src="../../assets/result-icon@2x.png" class="result-icon">
            <div class="result-infoBox">
                <div class="title">支付详情</div>
                <div class="txt"><span class="label">消费门店：</span><span class="input">{{orderInfo.storeName}}</span></div>
                <div class="txt"><span class="label">订单金额：</span><span class="input red">{{orderInfo.accountAmt}}元</span></div>
                <div class="txt"><span class="label">优惠券张数：</span><span class="input">{{orderInfo.couponCount}}张</span></div>
                <div class="txt"><span class="label">优惠券金额：</span><span class="input">{{orderInfo.totalCouponAmt}}元</span></div>
                <div class="txt"><span class="label">余额支付：</span><span class="input">{{orderInfo.balanceAmt}}元</span></div>
                <div class="txt"><span class="label">支付时间：</span><span class="input">{{orderInfo.payTime}}</span></div>
            </div>
        </div>
        <div class="page-result" v-if="orderInfo.isSuccess==0">
            <img src="../../assets/ico-fail.png" class="result-icon">
            <div class="result-infoBox">
                <div class="title">支付失败</div>
                <div class="txt">本商品已售罄，请选购其他商品</div>
                <a href="javascript:;" class="btn-pay" @click="clickBack">再试一次</a>
            </div>
        </div>
        <div class="page-result page-result-refresh" v-if="orderInfo.isSuccess==-1">
            <img src="../../assets/ico-try.svg" class="result-icon">
            <div class="result-infoBox">
                <div class="title">处理中</div>
                <a href="javascript:;" class="btn-pay" @click="clickRefrsh">点击刷新</a>
            </div>
        </div>
        <toast :showToast.sync="displayInfo.showToast" :txt="displayInfo.toastTxt" v-on:closeToast="closeToast"></toast>
        <!--loading-->
        <loading :show="displayInfo.showLoading"></loading>
    </div>
</template>
<!-- template for the modal component -->
    

<script>
function refreshRem(){
    var width=document.documentElement.getBoundingClientRect().width;
    var rem=width/10;
    document.documentElement.style.fontSize=rem+'px';
}
refreshRem();
var Vue = require('vue');
var VueResource = require('vue-resource');
Vue.use(VueResource);
var app={};
app.picBase="http://resources.piao.cmbchina.com/images/";
//=s import
import toast from '../../components/toast'
import loading from '../../components/loading'
import util from '../../common/js/util.js'
//=e import
var urlData=util.GetRequest();

export default {
    name: 'app',
     components: {
        toast,
        loading

            // 可以以key-value的形式注册组件, 此时挂载点的名字就是key
            // 否则挂载点和组件名字一致, 即vhead
            
        },
    data:function() {
      return {
        orderInfo:{
            isSuccess:'-1',
            chnlUserId:'3f43251f016a4336899010d27d521113',
            orderNo:'1605180000496042',
            storeName:'小南国（人民广场店）',
            storeNo:'9999006710002',
            accountAmt:'331',
            couponCount:'3',
            totalCouponAmt:'300',
            balanceAmt:'31',
            payTime:'2016-11-26',
            orderNo:'',
        },
        
        displayInfo:{
            showLoading:true,
            showToast:false,
            toastTxt:'',
        },

        styleObject: {
            height:'0px',
        },
      }
    },

    created:function () {
        var _this=this;
        this.orderInfo.orderNo=urlData.orderNo;
        var param=
        {
            "syshead": {
                "trans_code":"SI_PRD0006",
                'chnlId': '02',
               
            },
            "body": {
                "orderNo":this.orderInfo.orderNo,
            }
        }
        _this.displayInfo.showLoading = true;
        
        this.$http.post('/yummy-portal/JSONServer/execute.do',util.packMessage(param),{emulateJSON:true}).then(function(response){
            var data=response.data;
            console.log(data);
            _this.displayInfo.showLoading = false;
            if(data.syshead && data.syshead.rsp_code == "0000000000"){
                if(data.body.rows.length > 0){
                    _this.storeInfo.pageIndex++;
                    data.body.rows.forEach(function(item,index){
                        if(item.distance){
                            item.distance=Math.floor(item.distance*10)/10;
                            item.distance+='km';
                        }
                    });
                    _this.storeInfo.list=_this.storeInfo.list.concat(data.body.rows);
                }
            }else{
                _this.showToast(data.rsp_msg)
                //$.alert('系统繁忙，请稍后再试');
            }
        });
        
    },
    methods: {
        clickBack:function(){
            util.gotoScan();
        },
        clickRefrsh:function(){
            location.reload();
        },
        showToast:function(txt){
            this.displayInfo.showToast=false;
            this.displayInfo.showToast=true;
            this.displayInfo.toastTxt=txt;
        },
        closeToast:function(){
            this.displayInfo.showToast=false;
            this.displayInfo.showTxt='';
            console.log('closeToast')
        },
    },
        
}
</script>

<style lang="less">
</style>



