<template>
  <div id="app" v-cloak class="page-index">
    <div class="header"><span class="back-icon" @click="clickBack"></span> 买单</div>
    <div class="page-index" id="page-index">
        <div class="title">{{orderInfo.storeName}}</div>
        <div class="btn-change-store" @click="displayInfo.showStore=true"><span>更换门店</span></div>
        <div class="totalAmount">
            <span class="label">消费金额(元):</span>
            <input type="number" name="" class="amount input_number" v-model="orderInfo.accountAmt" @input="inputAmt">
            <i class="ico-delete" @click="clickDelete" v-show="displayInfo.showDelete"></i>
        </div>
        <div class="voucher-wrapper" v-show="displayInfo.showVoucherWrapper">
            <div class="voucher-checkbox" @click="clickCheckbox">
                <i class="ico-checkbox" v-bind:class="orderInfo.useVoucher?'selected':''"></i><span>使用代金券</span>
            </div>
            <div class="couponListBox"  :class="voucherInfo.showAll?'':'closed'" >
                <div class="couponListInfo J-couponListInfo">
                    <span class="arrow-left-icon" @click="voucherInfo.showAll=!voucherInfo.showAll"></span>
                    <span class="label">{{voucherInfo.title}}元代金券({{voucherInfo.list.length}}张可用)</span>
                    <div class="amount J-amountActionBox">
                        <span class="amountIconBox" @click="clickMinus"><i class="icon-minus" :class="voucherInfo.selectedNum<=0?'disable':''"></i></span>
                        <input type="number" v-model="voucherInfo.selectedNum" class="amountNumber J-amount-input">
                        <span class="amountIconBox" @click="clickPlus"><i class="icon-plus" :class="voucherInfo.selectedNum>=voucherInfo.list.length?'disable':''"></i></span>
                    </div>
                </div>
                <div class="couponList J-couponList" >
                    <div class="couponItem J-couponItem" v-for="(item,index) in voucherInfo.list">
                        <input type="checkbox" class="checkbox" :data-codeNo="item.codeNo" :id="item.codeNo" @click="clickVoucher(index)" v-model="item.isSelected">
                        <label :for="item.codeNo" class="label">
                            <p>代金券号：{{(item.codeNo.replace(/(\d{4})/g,'$1 ').replace(/\s*$/,''))}} </p>
                            <p>{{item.useDate}}</p>
                        </label>
                    </div>
                </div>
            </div>
        </div>
        <div class="needPay">
            <span class="label">还需付款(元):</span>
            <span class="amount">{{needAmt}}</span>
        </div>
        <a href="javascript:;" class="btn-pay" @click="handlePay" v-bind:class="orderInfo.accountAmt>0?'':'disable'">确认支付</a>
        <div class="info">请再次核对金额和门店信息</div>
    </div>
    <div class="store_wrapper" v-show="displayInfo.showStore" @click="displayInfo.showStore=false" >
        <div class="store_box" id="storeBox" @scroll="scrollStore" >
            <div class="title">选择您要消费的门店</div>
            <ul class="store_list">
                <li v-for="(item,index) in storeInfo.list" @click="selectStore(index)">
                    <div class="line1">
                        <p class="store_name">{{item.strName}}</p>
                        <p class="distance"><template v-if="index==0"><i class="ico_nearest"></i></template><span>{{item.distance}}</span></p> 
                    </div>
                    <div class="address_txt">{{item.address}}</div>
                </li>
            </ul>
        </div>
    </div>
    <!-- alert提示modal弹框 -->
    <modal :show.sync="displayInfo.showModal" :txt.sync="displayInfo.modalTxt" v-on:closeModal="closeModal"></modal>
    <!-- toast -->
    <toast :showToast.sync="displayInfo.showToast" :txt="displayInfo.toastTxt" v-on:closeToast="closeToast"></toast>
    <!--loading-->
    <loading :show="displayInfo.showLoading"></loading>
  </div>
</template>
<script>
function refreshRem(){
    var width=document.documentElement.getBoundingClientRect().width;
    var rem=width/10;
    document.documentElement.style.fontSize=rem+'px';
}
refreshRem();
var Vue = require('vue');
var VueResource = require('vue-resource');

Vue.use(VueResource);
var app={};
app.picBase="http://resources.piao.cmbchina.com/images/";





import modal from '../../components/modal'
import toast from '../../components/toast'
import loading from '../../components/loading'
import util from '../../common/js/util.js'

var urlData=util.GetRequest();
var getGPS=function(callback){
    var _this=this;
    var timestamp = Date.parse(new Date());
    window.location.href = "http://cmbls/gps?id=" + timestamp;      
    CMBLS = {};
    CMBLS.gps = {}
    CMBLS.gps.successCallback = function(id, message) {
        var messageArray=(message+"").split(">");
        var latitude=messageArray[2].split("<")[0];
        var longitude=messageArray[4].split("<")[0];
        _this.orderInfo.longitude=longitude.toString();
        _this.orderInfo.dimension=latitude.toString(); 
        util.setCookie('gps', JSON.stringify({longitude:longitude, latitude:latitude}));
        callback();
    }
    CMBLS.gps.failCallback = function(id, message) {
        util.setCookie('gps', JSON.stringify({longitude:'121.472644', latitude:'31.231706'}));
        callback();
    }
};
export default {
    name: 'app',
    components: {
            // 可以以key-value的形式注册组件, 此时挂载点的名字就是key
            // 否则挂载点和组件名字一致, 即vhead
            modal,
            toast,
            loading
        },
    data:function() {
      return {
        orderInfo:{
            chnlUserId:urlData.userId,
            merNo:urlData.merNo,
            //orderNo:'1605180000496042',
            storeName:urlData.storeName,
            storeNo:urlData.storeNo,
            accountAmt:'',
            productNo:urlData.productNo,
            cityNo:urlData.cityNo,
            useVoucher:false,
        },
        voucherInfo:{
            title: "10",
            selectedNum:0,
            showAll:false,
            list:[{
                "isSelected": false,
                //"halfPrice": "1",
                "ticketPrice": "10",
                "useDate": "有效期至：2016-12-28",
                "codeNo": "370226085205",
                //"productName": "金师傅是只小猪",
                //"merLogo": "20160622-152232_91_98_947ca28fdbf348339a75254d8be28fe3.jpg"
            },{
                "isSelected": false,
                "ticketPrice": "10",
                "useDate": "有效期至：2016-12-28",
                "codeNo": "370226085205",
            },{
                "isSelected": false,
                "ticketPrice": "10",
                "useDate": "有效期至：2016-12-28",
                "codeNo": "370226085205",
            },{
                "isSelected": false,
                "ticketPrice": "10",
                "useDate": "有效期至：2016-12-28",
                "codeNo": "370226085205",
            }]
        },
        storeInfo:{
            pageSize:10,
            pageIndex:1,
            longitude:'121.52402909999999',
            latitude:'31.244788699999997',
            list:[
            {
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'0.3km',
            },{
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'14.3km',
            },{
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'14.3km',
            },{
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'14.3km',
            },{
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'14.3km',
            },{
                strNo:'9999006710002',
                strName:'小南国（西郊百联店）',
                address:'仙霞西路8888号',
                distance:'14.3km',
            }
            ]
        },
        displayInfo:{
            //showVoucher:false,
            showStore:false,
            showDelete:false,
            showVoucherWrapper:false,
            showModal:false,
            modalTxt:'【优惠金额不能大于支付金额哦】',
            showToast:false,
            showLoading:true,
            toastTxt:'',
        },
        touchInfo:{
            startX:0,
            startY:0,
            endX:0,
            endY:0
        },
        styleObject: {
            height:'0px',
        },
        resList:[],
      }
    },

    computed: {
        discountAmt:function(){
            var discount=0;
            this.voucherInfo.list.forEach(function(item,index){
                if(item.isSelected==true){
                    discount+=(item.ticketPrice-0 );
                }
            });
            return discount;
        },
        needAmt: function () {
          if(this.voucherInfo.selectedNum==0){
            return this.orderInfo.accountAmt;
          }
          else {
            var _return=this.orderInfo.accountAmt-this.discountAmt;
            return _return>0?_return:0;
          }
        }
    },
    created:function () {
        // var urlData = util.GetRequest();
        if (!urlData.userId || !urlData.productNo) {
            util.gotoScan();
        }
        //getGPS(this.getStoreList);
        this.getVouchers();
        this.getStoreList();
    },
    mounted:function(){
    },
    methods: {
        clickMinus:function(){
            if(this.voucherInfo.selectedNum>0){
                for(var i=this.voucherInfo.list.length-1;i>=0;i--){
                    if(this.voucherInfo.list[i].isSelected==true){
                        this.voucherInfo.list[i].isSelected=false;
                        this.voucherInfo.selectedNum--;
                        break;
                    }
                }
                if(this.orderInfo.useVoucher==false)this.orderInfo.useVoucher=true;
            }
        },
        clickPlus:function(){
            if(this.voucherInfo.selectedNum<this.voucherInfo.list.length){
                for(var i=0,_length=this.voucherInfo.list.length;i<_length;i++){
                    if(this.voucherInfo.list[i].isSelected==false){
                        this.voucherInfo.list[i].isSelected=true;
                        this.voucherInfo.selectedNum++;
                        break;
                    }
                }
                if(this.orderInfo.useVoucher==false)this.orderInfo.useVoucher=true;
            }
        },
        clickCheckbox:function(){
            this.orderInfo.useVoucher=!!!this.orderInfo.useVoucher;
            this.selectVoucher();
            
        },
        clickDelete:function () {
            this.orderInfo.accountAmt='';
            this.displayInfo.showDelete=false;
        },
        clickBack:function(){
            util.gotoScan();
        },
        selectVoucher:function(){
            var _this=this;
            if(this.orderInfo.useVoucher){
                this.voucherInfo.list.forEach(function(item,index){
                    if((item.ticketPrice-0)<=(_this.needAmt-0) && _this.voucherInfo.list[index].isSelected==false){
                        _this.voucherInfo.list[index].isSelected=true;
                        console.log(_this.voucherInfo.list[index].isSelected);
                        _this.voucherInfo.selectedNum++;
                    }
                });
            }
            else {
                _this.voucherInfo.selectedNum=0;
                this.voucherInfo.list.forEach(function(item,index){
                    _this.voucherInfo.list[index].isSelected=false;
                });
            }
        },
        inputAmt:function() {
            if(this.orderInfo.accountAmt>0){
                this.displayInfo.showDelete=true;
                if(this.voucherInfo.list.length>0){
                    this.displayInfo.showVoucherWrapper=true;
                    this.selectVoucher();
                }
            }
            else {
                this.displayInfo.showDelete=false;
                this.displayInfo.showVoucherWrapper=false;
            }
        },
        clickVoucher:function(index){
            if(this.voucherInfo.list[index].isSelected){
                this.voucherInfo.list[index].isSelected=false;
                this.voucherInfo.selectedNum--;
            }
            else {
                this.voucherInfo.list[index].isSelected=true;
                this.voucherInfo.selectedNum++;
            }
        },
        selectStore:function(index){
            var storeSelected=this.storeInfo.list[index];
            this.orderInfo.storeName=storeSelected.strName;
            this.orderInfo.storeNo=storeSelected.strNo;

        },
        closeModal:function(){
            this.displayInfo.showModal=false;
        },
        showToast:function(txt){
            this.displayInfo.showToast=false;
            this.displayInfo.showToast=true;
            this.displayInfo.toastTxt=txt;
        },
        closeToast:function(){
            this.displayInfo.showToast=false;
            this.displayInfo.showTxt='';
            console.log('closeToast')
        },
        getStoreList:function(){
            var _this=this;
            var param=
            {
                "syshead": {
                    "trans_code":"SI_PRD0006",
                    'chnlId': '31',
                    "pageSize":this.storeInfo.pageSize,
                    "pageIndex":this.storeInfo.pageIndex,
                },
                "body": {
                    "productNo":this.orderInfo.productNo,
                    "cityNo":this.orderInfo.cityNo,
                    "longitude":this.storeInfo.longitude, 
                    "dimension":this.storeInfo.latitude
                }
            }
            _this.displayInfo.showLoading = true;
            
            this.$http.post('/yummy-portal/JSONServer/execute.do',util.packMessage(param),{emulateJSON:true}).then(function(response){
                console.log(response.data);
                var data=response.data;
                _this.displayInfo.showLoading = false;
                if(data.syshead && data.syshead.rsp_code == "0000000000"){
                    if(data.body.rows.length > 0){
                        _this.storeInfo.pageIndex++;
                        data.body.rows.forEach(function(item,index){
                            if(item.distance){
                                item.distance=Math.floor(item.distance*10)/10;
                                item.distance+='km';
                            }
                        });
                        _this.storeInfo.list=_this.storeInfo.list.concat(data.body.rows);
                    }
                }else{
                    this.showToast(data.rsp_msg)
                    //$.alert('系统繁忙，请稍后再试');
                }
            });
        },
        getVouchers:function(){//获取券列表
            var _this=this;
            var param=
            {
                "syshead": {
                    "trans_code": "TablePayQryAvailableCoupons",
                    "chnlId":"02",
                    "chnlUserId":this.orderInfo.chnlUserId,
                },
                "body": {
                    merNo:this.orderInfo.merNo,
                    strNo:this.orderInfo.storeNo,
                    //storeName:this.orderInfo.storeName,
                    accountAmt:this.orderInfo.accountAmt+'',
                }
            }
            
            _this.displayInfo.showLoading = true;
            this.$http.get('/yummy-portal/JSONServer/execute.do',{params:util.param2String(param)}).then(function(response){
                console.log(response.data);
                var data=response.data;
                _this.displayInfo.showLoading = false;
                //this.orderInfo.useVoucher=true;

                if(data.syshead && data.syshead.rsp_code == "0000000000") {
                    _this.voucherInfo.list=data.body.rows[0].list;
                    if(data.body.rows[0].list.length>0){
                        this.orderInfo.useVoucher=true;
                        data.body.rows[0].list.forEach(function(item,index){
                            if(!_this.voucherInfo.list[index]){
                                _this.voucherInfo.list.push({});
                            }
                            _this.voucherInfo.list[index].isSelected=false;
                            _this.voucherInfo.list[index].ticketPrice=item.ticketPrice;
                            _this.voucherInfo.list[index].useDate=item.useDate;
                            _this.voucherInfo.list[index].codeNo=item.codeNo;
                        });
                    }
                    
                }
                else {
                    this.showToast(data.rsp_msg)
                } 
            });
        },
        handlePay:function(){
            var _this=this;
            if(this.needAmt==0 && this.discountAmt>this.orderInfo.accountAmt){
                this.displayInfo.showModal=true;
                return;
            }
            var param=
            {
                "syshead": {
                    "chnlUserId":this.orderInfo.chnlUserId,
                    "trans_code": "TablePayConfirmPayment",
                    "chnlId":"02",
                },
                "body": {
                    "merNo":this.orderInfo.merNo,
                    "strNo":this.orderInfo.storeNo,
                    "storeName":this.orderInfo.storeName,
                    "accountAmt":this.orderInfo.accountAmt,
                    "sessionId":"",
                    "codeNos":"",
                }
            }
            var arrCode=[];
            if(this.orderInfo.useVoucher){
                this.voucherInfo.list.forEach(function(item,index){
                    if(item.isSelected){
                        arrCode.push(item.codeNo);
                    }
                });
                param.body.codeNos=arrCode.join(',');
            }
            
            _this.displayInfo.showLoading = true;
            this.$http.get('/yummy-portal/JSONServer/execute.do',{params:util.param2String(param)}).then(function(response){
                console.log(response.data);
                _this.displayInfo.showLoading = false;

                var data=response.data;
                if(data.body && data.body.reqPayUrl) {
                    var link = data.body.reqPayUrl;
                    window.location.href = link;
                }
                else {
                    this.showToast(data.rsp_msg)
                } 
            });
        },
        scrollStore:function(){
            var ele=document.getElementById('storeBox');
            var sTop=ele.scrollTop,
            sHeight=ele.scrollHeight,
            cHeight=ele.clientHeight;
            if(sHeight-sTop-cHeight<1){
                this.getStoreList();
            }
        },
        
    },
}
</script>

<style lang="less">
@import 'app.less';
</style>



