<template id="modal-template"> 
<div class="modal-mask" v-show="show">
  <div class="modal-wrapper">
    <div class="modal-container">
      <div class="modal-title">
        <slot name="title">
          <span>提示</span>
        </slot>
      </div>
      <div class="modal-body">
        <slot name="body">
          <div class="modal-txt">{{txt}}</div>
        </slot>
      </div>

      <div class="modal-footer" >
        <slot name="footer">
          <span class="modal-default-close" @click="closeModal">确定</span>
        </slot>
      </div>
    </div>
  </div>
</div>
  
</template>
<script>
var Vue = require('vue');
export default {
  template: '#modal-template',
  props: {
      show: {
        type: Boolean,
        required: true,
        twoWay: true    
      },
      txt: {
        type: String,
        required: false,
        twoWay: true    
      },
  },
  // data: function () {
  //   return { 
  //     show: this.initialShow
  //   }
  // },
  methods:{
    closeModal:function(){
      this.show=false;
      this.$emit( 'closeModal',false);
    }
  }
}
</script>

<style >
.modal-mask {
  position:absolute;
  display:table;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background: rgba(0,0,0,0.6);
  z-index: 9;
  text-align: center;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display:table-cell;
  vertical-align:middle;
  text-align:center;
  color:#565656;
}
.modal-container {
  display: inline-block;
  width:8rem;
  padding-top: .64rem/* 24px */;
  border-radius: .06666667rem/* 2.5px */;
  background:#FFF;
}
.modal-title{
  font-size:.42666667rem/* 16px */;
  margin-bottom: .32rem/* 12px */;
}
.modal-txt {
  margin-bottom:.58666667rem/* 22px */;
  font-size:.37333333rem/* 14px */;
}
.modal-footer {
  height:1.17333333rem/* 44px */;
  line-height: 1.17333333rem/* 44px */;
  border-top:1px solid #ccc;
  color:#0074E1;
  font-size: .42666667rem/* 16px */;
}
.modal-default-close {
  display: block;

}
</style>



